@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#background {
  position: fixed;
  height: 100vh;
  width: 100vw;
  background-image: url(/static/media/starrynight.5d689918.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 0;
}

.App {
  display: grid;
}

.lucas-content {
  z-index: 1;
}

.lucas-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 1;
}

.center-column {
  width: 95.5%;
}

.column-half {
  width: 50%;
}

@media only screen and (min-width: 1240px) {
  .center-column {
    width: 1170px;
  } 
}
#introductions {
    flex-direction: column;
}

.homepage-nav {
    display: flex;
    flex-direction: row;
    align-items: center;
}

.homepage-nav .MuiFab-root {
    font-size: 1.5rem;
    width: 3rem;
    height: 3rem;
    min-width: 3rem;
    min-height: 3rem;
    margin-left: 16px;
    box-shadow: none;
    
}

.homepage-nav .MuiCardContent-root {
    margin-left: 2%;
}

@media only screen and (min-width: 1240px) {
    .homepage-nav-group {
        display: flex;
        flex-direction: row;
        width: 100%;
    }
}
#portrait {
    border-radius: 0.25rem;
}

@media only screen and (min-width: 1240px) {
    #about .homepage-section-body {
        display: flex;
    }
    
    #about .homepage-section-body .homepage-content {
        margin-bottom: 0;
    }
    
    .homepage-about {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        height: 100%;
    }
}
#contact {
    margin-bottom: 10vh;
}

.homepage-contact-fabs {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

.homepage-contact-fabs .MuiFab-root {
    font-size: 2.5rem;
    width: 5rem;
    height: 5rem;
    min-width: 5rem;
    min-height: 5rem;
    margin-left: 5vw;
    margin-right: 5vw;
    box-shadow: none;
}

@media only screen and (min-width: 1240px) {
    .homepage-contact-fabs .MuiFab-root {
        font-size: 3rem;
        width: 6rem;
        height: 6rem;
        min-width: 6rem;
        min-height: 6rem;
    }
}
.lucas-page {
    width: 100%;
    padding: 0
}

.lucas-page .MuiCardContent-root:last-child {
    padding-bottom: 16px;
}

.homepage-home {
    height: 88vh;
    display: flex;
    justify-content: center;
}

.homepage-big-text {
    position: relative;
    top: 12vh;
    font-size: 14vw;
    font-family: 'Raleway', sans-serif;
    font-weight: 200;
    text-align: center;
    color: white;
}

#homepage-logo-horz {
    display: none;
}

#homepage-logo-vert {
    object-fit: contain;
    position: relative;
    width: 60%;
    height: auto;
}

.homepage-content {
    margin: 2%;
}

.homepage-content img {
    width: 100%;
    object-fit: cover;
}

.homepage-section-title {
    font-family: 'Raleway', sans-serif;
    font-weight: 200;
    font-size: 12vw;
    text-align: center;
    color: white;
}

.homepage-subtitle {
    font-family: 'Raleway', sans-serif;
    font-weight: 400;
    font-size: xx-large;
    text-align: center;
    color: white;
}

.homepage-divider {
    color: white;
    width: 95%;
    margin-top: 32px;
    margin-bottom: 32px;
}

.homepage-section {
    width: 95%;
}

@media only screen and (min-width: 1240px) {

    .homepage-big-text {
        position: static;
        font-size: 14vh;
    }

    #homepage-logo-horz {
        display: block;
        object-fit: contain;
        position: relative;
        bottom: 10vh;
    }

    #homepage-logo-vert {
        display: none;
    }

    .homepage-half {
        width: 50%;
        position: relative;
    }

    .homepage-divider {
        width: 1170px
    }

    .homepage-section-title {
        font-size: 10vh;
    }

    .homepage-section {
        width: 1170px;
    }

    .homepage-section-body {
        display: flex;
        flex-direction: row;
    }
}
.timeline-visual {
    object-fit: contain;
    width: 100%;
    border-radius: 0.25rem;
}
.timeline-button-container {
    margin-top: 20px;
    margin-bottom: 20px;
    display: flex;
    justify-content: space-around;
}

.timeline-button-container .MuiFab-root {
    font-size: 1.5rem;
    width: 3rem;
    height: 3rem;
    box-shadow: none;
    text-align: center;
}

@media only screen and (min-width: 1240px) {
    .timeline-button-container {
        flex-direction: row;
        width: 45%;
        position: absolute;
        bottom: 0px;
    }
}
.timeline-date {
    color: gray;
}

.timeline-text {
    color: gray;
    padding-left: 3%;
}

.timeline-text h3 {
    color: rgb(69, 69, 69);
}

.SnapImg {
    object-fit: contain;
    width: 100%;
}

@media only screen and (min-width: 1240px) {
    .timeline-body {
        display: flex;
        flex-direction: row;
    }

    .timeline-half {
        width: 50%;
    }
}

.MuiFormControlLabel-root{
    padding-top: 12px;
    width: 60px;
}
.timeline-page {
    padding-top: 3%;
}

@media only screen and (min-width: 1240px) {
    .timeline-page {
        padding-top: 1%;
    }
}
